* {
    line-height: calc(1em + 1ex);
}




ul, ol, li {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
}

/* ===== Scrollbar CSS ===== */

 /* Firefox */

 * {
    scrollbar-width: auto;
    scrollbar-color: #aaaaaa #ffffff;
   }
  
   /* Chrome, Edge, and Safari */
   *::-webkit-scrollbar {
    width: 11px;
   }
  
   *::-webkit-scrollbar-track {
    background: #ffffff;
   }
  
   *::-webkit-scrollbar-thumb {
    background-color: #aaaaaa;
    border-radius: 10px;
    border: 3px solid #ffffff;
   }


body {
    font-family: "Work Sans", sans-serif !important;
    font-weight: normal;
}





.password-wrapper {
    position: relative;
    .toggle-password {
        position: absolute;
        
        font-weight: 700;
        background: none;
        border: none;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        display: flex;    
        span {
            color: #7EA1F3;
            font-size: 30px;
            font-weight: bold;
        }    
    }
}

.main__wrapper {
    display: grid;
    grid-template-columns: 250px 1fr;
    background-color: #F5F5F5;
    min-height: 100vh;
    transition: 0.3s;
    &.full-width {
        grid-template-columns: 1fr;
        .sidebar {
            display: none;
        }
        .main-header {
            background: #F5F6FA;
            z-index: 2;
           
            .logo {
                display: flex;
                align-items: center;
                min-width: none;
                flex-grow: 0;
                @media screen and (max-width: 768px) {
                    flex-grow: 1;
                    min-width: 100%;
                    margin: 0 auto;
                    display: block;
                    display: flex;
                    justify-content: center;                    
                }
            }
        }
        .main__content {
            background: #fff;
        }
        &[data-collapse=true] {
            grid-template-columns: 1fr;
        }
    }
   
}


.main__wrapper  {

    .logo-open {
        display: block;
    }

    .logo-collapsed {
        display: none;
    }

    &[data-collapse=true] {
        grid-template-columns: 60px 1fr;
        .main-nav__link {
            padding-inline: 0;
            display: flex;
            justify-content: center;
            &:hover {
                .main-nav__link-text {
                    opacity: 1;
                    visibility: visible;
                }
            }
        }
        .main-nav__link-text {
            background: #fff;
            opacity: 0;
            visibility: hidden;
        }

        .logo-open {
            display: none;
        }
    
        .logo-collapsed {
            display: block;
        }        
    }
}

.docs-page {
    .menu__toggle {
        display: none;
    }
}

.main-nav__docs {

    .main-nav__link {
        padding: 0;
    }

    .main-nav__submenu {
        li {
            padding-left: 16px;
            &:hover {
                background-color: #ECECF8;
                color: #3A7DE8;    
                transition: 0.3s;
                a {
                    transition: 0.3s;
                    color: #3A7DE8;
                }            
            }
        }
        a {
            color: #6E6E6E;
            font-size: 13px;
            text-decoration: none;
            font-weight: 500;
        }
    }
}

.docs-section {
    font-size: 14px;
    line-height: 20px;
    color: #77787B;
    ~ .docs-section {
        margin-top: 30px;
    }
    p {
        margin-bottom: 5px;   

    }
}




.main__content {
    background-color: #F5F6FA;
}


.main-header {
    background: var(--gray-300);
    padding-block: 22px;
    &__logo {
        display: flex;
        align-items: center;
    }    
    .logo {
        display: none;
    }
}

.main-header__inner {
    padding-inline: 40px;
    gap: 15px;
    flex-wrap: wrap;
    @media screen and (max-width: 768px) {
        flex-wrap: wrap;
        padding-inline: 10px;
    }
}

.main-header--inner-page {
    background: #fff;
    z-index: 1;    
    @media screen and (min-width: 1200px) {
        position: sticky;
        top: 0;

    }
}

.user-dropdown-menu {
    background: #FFFFFF;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    padding-block: 20px;
    min-width: 205px;
    border: none;
    .dropdown-item {
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        color: #7EA1F3;
        padding-inline:  25px;
        display: flex;
        align-items: center;
        transition: 400ms;
        .icon {
            font-size: 22px;
            margin-right: 5px;
        }
        

    }
    .text-item {
        padding-inline:  25px;   
    }
}

.apps-dropdown-menu {
    background: #FFFFFF;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    padding-block: 20px;
    min-width: 205px;
    border: none;
    .text-item .text-item__title {
        font-weight: 600;
        color: var(--blue-300);        
    }
}



.notifications-list {
    background: #FFFFFF;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    padding: 20px;  
    min-width: 300px;
    li {
        font-size: 14px;
        color: #838383;
        margin-top: 10px;
        padding-bottom: 10px;
        border-bottom: solid 1px #cacaca;
    }  
}

.btn--notifications {
    color: var(--blue-300);
    position: relative;
    display: flex;
    padding-inline: 0;
    .notification__alert {
        display: block;
        width: 15px;
        height: 15px;
        background: var(--red-300);
        position: absolute;
        border-radius: 50%;
        border: solid 3px #fff;
        right: 0;
        top: 2px;        
    }
}

.btn--apps {
    width: 46px;
    height: 46px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.btn--goal {
    height: 36px;
    display: flex;
    align-items: center;
    min-width: 200px;
    justify-content: space-between;
}


.user__wrapper {
    display: flex;
    align-items: center;
    text-align: left;
    padding-right: 0;

    .user__avatar {
        margin-right: 10px;
    }
}


.user__name {display: block;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;    
    color: #3A7DE8;    
}

.user__score {
    display: block; 
    font-weight: 500;
    font-size: 9px;
    line-height: 11px;
    color: #838383;
}

.search__wrapper {
    max-width: 960px;
    padding-inline: 20px;
    padding-block: 20px;  
    margin-inline: auto;
    display: flex;
    flex-direction: column;  
    @media screen and (min-width: 1200px) {
        padding-inline: 40px;
        padding-block: 90px;              
    }  
}

.search__wrapper > * + * {
    margin-top: 30px;
}

.search__title {
    color: var(--purple-800);
    font-size: 32px;
    line-height: 38px;
    text-align: center;
    font-weight: 300;
}

.search__fields {
    display: flex;
    gap: 15px;
    
    background: var(--white);
    border: 1px solid var(--purple-300);
    box-shadow: 0 2px 10px rgba(71, 46, 140, 0.1);
    border-radius: 3px;
    padding: 8px 16px;  
    flex-wrap: wrap;  


    .btn:not(.dropdown-toggle) {
        height: 36px;
        width: 36px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    @media screen and (max-width: 1200px) {
  
       > * {
        flex-grow: 0;
        &:first-child {
            flex-grow: 1;
            min-width: 100%;
           }        
       }
    }
}

.search__field {
    flex: 1;
    padding-inline-start: 0;
    border: none;
}

.search_batch {
    font-style: normal;
    font-weight: 600;
    font-size: 11px;
    line-height: 13px;
    text-align: center;
    text-transform: uppercase;
    color: #7EA1F3;
    text-decoration: none;
    margin-inline: auto;
}


.services__wrapper {
    background-color: var(--gray-300);
    border-radius: 8px;
    padding: 64px;
}

.search__wrapper--inner-page {
    flex: 1;
    max-width: 750px;
    background: #fff;
    // @media screen and (min-width: 1200px) {
    //     margin-right: 80px;

    // }
    .search__fields {
        background: #FFFFFF;
        border: 1px solid #EEEEEE;
        border-radius: 5px;
        box-shadow: none;
    }
}

.goals-list {
    min-width: 320px;
    background: #FFFFFF;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    padding: 20px; 
    max-height: 300px;
    color: #6E6E6E;
    overflow: auto;      
    border: none; 
    .goal-item {
        cursor: pointer;
        padding: 8px;
        border-radius: 5px;
        &.selected  {
            background: #eee;
        }
    }
    dt {
        font-weight: 600;
        font-size: 15px;        
    }
    dd {
        font-size: 13px;
        color: #212529;        
    }
}

.sub-header {
    background: #fff;
    border-bottom: solid 3px #3A7DE8;
    padding-inline: 40px;
    display: none;
    @media screen and (min-width: 1200px) {
        padding-block-start: 15px;
        padding-block-end: 25px;
        display: flex;
        align-items: center;
        justify-content: space-between;        

    }
} 

.leads-slider__wrapper {
    max-width: 750px;
    display: block;
    ul {
        gap: 10px;
    }
    a {
        text-decoration: none;
    }
    .lead {
        background: #ECECF8;
        border-radius: 3px;
        padding: 10px;
        display: flex;
        align-items: center;
        &__icon {
            color: #3A7DE8;
            display: flex;   
            margin-right: 5px;         
        }
        &__name {
            font-weight: 400;
            font-size: 12px;
            line-height: 14px;
            color: #3A7DE8;
            margin-bottom: 0;
        }
        &__document {
            font-weight: 400;
            font-size: 9px;
            line-height: 11px;
            color: #838383;
            margin-bottom: 0;
        }
    }
    .slick-slide {
        padding-inline: 10px;
    }
}

/* Breadcrumbs */
.breadcrumb__wrapper {
    background: #ECECF8;
    padding-inline: 40px;
    padding-block: 25px;
}

.breadcrumb__list {
    display: flex; 
    align-items: center;
}

.breadcrumb__item {
    display: flex; 
    align-items: center;
    position: relative;
    color: #6B5FC6;
    font-size: 16px;
    &.has--icon {
        span {
            margin-right: 5px;
        }
    }

    &:not(:last-child) {
        &:after {
            content: "\e5cc";
            display: inline-flex;
            margin: 0 5px;
        }
    }   
} 

.breadcrumb__link {
    color: #6B5FC6;
    text-decoration: none;
    &:hover {
        color: inherit;
        text-decoration: underline;
    }
}



.lead-selected__wrapper {
    background: #ECECF8;
    padding-inline: 40px;
    padding-block-end: 30px;
    @media screen and (min-width: 1200px) {
        padding-inline: 40px;        
        display: flex;
        justify-content: space-between;        
    }
}

.lead-selected-info {
    @media screen and (min-width: 1200px) {
        max-width: 50%;
    }
}


.lead-selected__name {
    font-weight: 400;
    font-size: 28px;
    line-height: 33px;
    color: #001B95;
}

.lead-selected-info__details {
    @media screen and (min-width: 1200px) {

        display: grid;
        grid-template-columns: 1fr 1fr;
    }
}



.lead-selected__document {
    font-size: 14px;
    color: #A0A1A5;
    display: flex;
    .lead-selected__document__number {
        color: #222326;
        margin-inline-start: 10px;
        align-self: flex-start;
    }
}

.lead-selected__gender {
    font-size: 14px;
    color: #A0A1A5;
    align-items: center;
    grid-row: span 2;
    display: flex;
    gap: 10px;
    @media screen and (min-width: 1200px) {
        display: block;
        margin-inline-start: 90px;
        
    }
    .gender {
        color: #222326;
        display: block;    
        font-size: 16px;   
    }
}

.lead-selected__additional-wrapper {
    @media screen and (min-width: 1200px) {
        display: flex;
        gap: 30px;

    }    
}

.lead-selected__additional-info {
    background: #FFFFFF;
    border-radius: 10px;
    padding: 20px;    
    align-items: center;
    margin-block: 20px;
    display: flex;
    flex-wrap: wrap;
    @media screen and (min-width: 1200px) {
        max-width: fit-content;
        margin-block: 0;
        min-width: 250px;
    }
   
    .icon {
        margin-right: 20px;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;        
    }
    .label {
        font-weight: 500;
        font-size: 12px;
        line-height: 16px;
        color: #A0A1A5;
    }
    .info {
        font-size: 18px;
    }
    p {
        margin-bottom: 0;
    }
    .text {
        font-size: 18px;
        line-height: 23px;
        text-decoration: none;
        color: inherit;
        &.has--icon {

            &:after {
                content: "\e5cc";
                font-weight: bold;
            }
        }
    }
    &[variant="blue"] {
        .icon {
            color: #3A7DE8;
            background: #E2E8FC;

        }
        .info {
            color: #3A7DE8;
        }
    }
    &[variant="green"] {
        .icon {
            color: #00CB88;
            background: #E1F9F1;
        }
        .info {
            color: #00CB88;
        }
    }

    &[variant="purple"] {
        .icon {
            color: #6B5FC6;
            background: #ECECF8;
        }
        .info {
            color: #444448;
        }
    }   
    
    &[variant="gray"] {
        .icon {
            color: #6E6E6E;
            background: #F6F6F6;
        }
        .info {
            color: #444448;
        }
    }       

}

.lead-selected__complementary-info {
    background: #ECECF8;
    padding-inline: 40px;
    padding-block: 20px;
    position: relative;
    @media screen and (min-width: 1200px) {
        &:before {
            content: "";
            width: calc(100% - 80px);
            position: absolute;
            top: 0;
            height: 1px;
            background: #D0D0EF;
        }    
    }
}

.info-list {
    position: relative;
    
    @media screen and (min-width: 1200px) {
        display: flex;

    }

    .label {
        font-weight: 500;
        font-size: 14px;
        line-height: 16px;
        color: #A0A1A5;
        display: block;
        margin-bottom: 8px;
    }
    .text {
        font-size: 14px;
        margin-bottom: 0;
        &.inline {
            .label {
                display: inline-block;
                margin-inline-end: 30px;
                min-width: 30px;
            }
            .text {
                padding: 4px 8px; 
                span {
                    display: inline-block;
                    padding: 4px 8px;
                }
                       
            }
        }
    }
    li {
        position: relative;
        
        &:not(:first-child) {
            @media screen and (min-width: 1200px) {
            padding-inline-start: 50px;
            }
        }
        &:last-child {
            margin-inline-end: 0;
            @media screen and (min-width: 1200px) {
                &:not(.no-border) {

                    &:before  {
                        content: "";
                        width: 1px;
                        height: 100%;
                        position: absolute;
                        background: #D0D0EF;
                        left: 0;
                    }
                }
            }

        }
    }
    .mother__name {
        background: #E2E8FC;
        border-radius: 5px;
        display: inline-flex;
        align-items: center;
        padding: 4px 8px;
        text-decoration: none;
        &:after {
            content: "\f02f";
            font-size: 21px;
            margin-left: 5px;     
            font-weight: bold;       
        }
                    
    }


}

.badge {
    font-size: 10px;
    font-weight: 500;
    color: #222326;
    background: #27DEAD;
    padding-block: 1px;
    padding-inline: 8px;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    margin-left: 20px;
    text-decoration: none;
    &:first-child {
        margin-left: 15px;
    }
    &__icon {
        font-size: 18px;
        font-weight: bold;
        margin-right: 4px;
    }     
}

.badge-green {
    background-color: #E1F9F1;
    color: #00CB88;
}
.badge-purple {
    background-color: #ECECF8;
    color: #6456BB;
} 


.auxiliary-buttons {
    display: flex;
    gap: 20px;
    .btn {
        width: 36px;
        height: 36px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

.sidebar {
    padding: 16px;
    background-color: #fff;
    position: relative;
    box-shadow: 1px 0 7px 0 rgba(0,0,0,0.1);
    z-index: 2;
    .main-nav {

        z-index: 2;
        max-height: calc(100vh - 65px);
        overflow: auto; 
        position: fixed;    

        [data-collapse='true'] & {
            // position: relative;
            // margin-right: -15px;
            width: 44px;
            overflow-x: hidden;
        }
    }
    .sidebar__header {
        position: sticky;
        background: #fff;
        margin: -16px -16px 0;
        z-index: 3;
        padding: 16px;
        top: 0;        
    }

}

.menu__toggle {
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    padding: 0;
    [data-collapse='true'] & {
        position: absolute;
        left: 100%;
        z-index: 2;
        border-radius: 0 4px 4px 0;
        box-shadow: 1px 0 3px 2px rgba(0,0,0,0.1);  
        span {
            transform: scaleX(-1);
        }
    }    
}


.main-nav__item {
    + .main-nav__item {
        margin-top: 5px;
    }    
}

.main-nav__link {
    font-weight: 400;
    font-size: 13px;
    line-height: 20px;
    letter-spacing: -0.5px;
    color: #6E6E6E;
    text-decoration: none;
    padding-block: 8px;
    padding-inline: 16px;
    border-radius: 3px;
    display: flex;
    transition: 0.4s;
    align-items: center;
    span {
        color: #D0D0EF;
        transition: inherit;
        margin-right: 5px;
    }
    .main-nav__link-text {
        transition: 0.3s;
        white-space: nowrap;
        
        border-radius: 0 4px 4px 0;
        padding: 3px;       
        color: #6E6E6E;
        font-weight: 500;   
        
        [data-collapse=true] &{
            display: none;
        }        
        
    }
    &:hover,
    &.active,
    &.active:hover {
        background-color: #ECECF8;
        color: #3A7DE8;
        span {
            color: inherit;
            
        }
    }
    &.active {
        &:after {
            content: "\e5cc";
            font-weight: bold;
            font-size: 18px;
        }            
    }    
}

.tooltip-menu {
    position: fixed;
    z-index: 3;
    background-color: #fff;
    padding: 5px 10px;
    color: #3A7DE8;
    font-size: 14px;
    height: 40px;
    background: #fff;
    display: flex;
    align-items: center;
    /* font-weight: bold; */
    // box-shadow: 0 3px 5px rgba(0,0,0,0.5);
    border-radius: 0 5px 5px 0;
    overflow: hidden;


    [data-collapse=false] & {
        display: none;
    }    
}

.services__title {
    font-weight: 600;
    font-size: 27px;
    line-height: 32px;
    text-align: center;
    color: var(--gray-800);
}

.services_cards {
    margin-block-start: 60px;
}

.service__card {
    background: transparent;
    height: 100%;
    border: none;
    .card-title {
        font-weight: 700;
        font-size: 24px;
        line-height: 28px;
        color: #3A7DE8;
    }
    .card-subtitle {
        font-weight: 400;
        font-size: 16px;
        line-height: 22px;
        color: #222326;
        margin-bottom: 12px;
    }
    .card-body {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    .card-text {
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        color: #77787B;
        flex: 1;
    }
    &__more {
        font-weight: 400;
        font-size: 13px;
        line-height: 15px;
        color: #3A7DE8;
        text-decoration: none;
        display: flex;
        &:after {
            content: "\e89e";
            display: inline-block;
            font-family: "Material Symbols Outlined";
            margin-left: 10px;
            font-size: 20px;
            font-weight: bold;
        }
    }
}

.news__wrapper {
    background-color: var(--gray-300);
    border-radius: 8px;
    padding: 32px 28px 64px;
    .slick-dots {
        bottom: 0;
        top: calc(100% + 15px);
    }
}

.news-cards__wrapper {
    margin-top: 24px;
    > * + * {
        margin-top: 10px;
    }
    .slick-slide {
        > div {
            margin-top: 10px;
        }
    }
}


.slider-news-modal {
    .btn-close {
        right: 10px;
        position: absolute;
        top: 10px;
        z-index: 1;
    }
}

.slider-news-modal {
    .slick-dots {
        position: static;
        margin-bottom: 0;
        li {
            button {
                margin: 0 auto;
            }
        }
    }
    .modal-body {
        padding: 30px;
    }    
}

.custom-controls {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    .custom-controls-inner {
        position: relative;
        display: flex;
        align-items: center;        
    }
    .slick-arrow {
