.progress-bar {
    position: relative;
    overflow: initial;
    border-radius: .375rem;
    text-indent: -9999px;
    transition: 0.7s;
    &:before {
        content: "";
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 5px;
        border-color:  transparent transparent #00CB88 transparent;
        right: 0;
        transform: translate(50%, 10px);
        position: absolute;
        
    }
    &:after {
        content: attr(aria-valuenow)'%';
        height: 30px;
        position: absolute;
        left: 100%;
        top: 100%;
        transform: translateX(-50%);
        text-indent: 0;
        font-weight: 700;
        font-size: 13px;
        line-height: 15px;
        text-align: center;
        color: #27DEAD;
        display: flex;
        align-items: flex-end;
    }
}

