.multi-step-form__progress-wrapper {
    width: 100%;
    padding: 20px;
    margin-bottom: 20px;
    .progress {
        margin-inline: 25px;
        height: 8px;
    }
    .progress-bar {
        background-color: var(--green-300);
        transition: 700ms;
        transition-property: width;
        &:before,
        &:after {
            display: none;
        }
    }
}

.steps-indicator {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: -25px;
    position: relative;
    &__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        flex-basis: 65px;
        text-align: center;
        &.active  {
            .steps-indicator__number {
                background: var(--green-300);
            }
        }
    }
    &__number {
        font-weight: bold;
        background: var(--purple-800);
        color: #fff;
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 100%;
        transition: 0.5s;
        transition-property: background;
    }
    &__text {
        font-size: 14px;
        text-align: center;
    }
}

