.plans-wrapper .plan-item {
    background: #fff;
    padding: 8px;
    border-radius: 8px;
    box-shadow: 0 0 3px 3px rgba(0,0,0,0.05);
    flex-basis: 200px;
    cursor: pointer;
    transition: 0.3s;
    overflow: hidden;
    flex-grow: 1;
    @media screen and (min-width: 1024px) {
        flex-grow: 0;
    }
    .btn {
        visibility: hidden;
        opacity: 1;
        transition: 0.3s;
    }
    &:hover,
    &.active {
        background: #ECECF8;
        color: #6456BB;
        box-shadow: 0 0 5px 5px rgba(0,0,0,0.1);
        transform: scale(1.05);
        .plan-item__header {
            // background: #27DEAD;
            background: #27DEAD;
        }
        .btn, .btn:hover {
            background: transparent;
            color: var(--blue-300) !important;
     
            
        }
    }
    &.active {
        .btn {
            visibility: visible;
            opacity: 1;
            
        }
    }

}

